body {
    width: 100%;
    height: 100%;
    background-color: #000000; /* Default background color */
    margin: 0;
    padding: 0;
    overflow: hidden;
    /* Gradient applied on top of the black background */
}

/* Position the button VR on the bottom of the page. */

#ui {
    position: absolute;
    bottom: 10px;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
    font-family: 'Karla', sans-serif;
    z-index: 1;
}

a#magic-window {
    display: block;
    color: white;
    margin-top: 1em;
}

